import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Route, Routes } from "react-router-dom";
import Admin from "./components/Auth/Admin";
import Auth from "./components/Auth/Auth";
import Booking from "./components/Bookings/Booking";
import Header from "./components/Header";
import HomePage from "./components/HomePage";
import AddMovie from "./components/Movies/AddMovie";
import EditMovie from "./components/Movies/EditMovie";
import DeleteMovie from "./components/Movies/DeleteMovie";
import AddTheater from "./components/Theaters/AddTheater";
import EditTheater from "./components/Theaters/EditTheater";
import DeleteTheater from "./components/Theaters/DeleteTheater";
import Movies from "./components/Movies/Movies";
import AdminProfile from "./profile/AdminProfile";
import UserProfile from "./profile/UserProfile";
import { adminActions, userActions } from "./store";
import Theaters from "./components/Theaters/Theaters";
import { CityProvider } from './components/CityContext';
import MovieListings from './components/MovieAnalytics';
import LocationAnalytics from './components/LocationAnalytics';
import AddShowtime from "./AddShowtimes";

function App() {
  //require('dotenv').config();
  const dispatch = useDispatch();
  const isAdminLoggedIn = useSelector((state) => state.admin.isLoggedIn);
  const isUserLoggedIn = useSelector((state) => state.user.isLoggedIn);
  console.log("isAdminLoggedIn", isAdminLoggedIn);
  console.log("isUserLoggedIn", isUserLoggedIn);
  useEffect(() => {
    if (localStorage.getItem("userId")) {
      dispatch(userActions.login());
    } else if (localStorage.getItem("adminId")) {
      dispatch(adminActions.login());
    }
  }, [dispatch]);
  return (
    <CityProvider>
      <div>
        <Header />
        <section>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/movies" element={<Movies />} />
            <Route path="/theaters" element={<Theaters />} />
            {!isUserLoggedIn && !isAdminLoggedIn && (
              <>
                {" "}
                <Route path="/admin" element={<Admin />} />
                <Route path="/auth" element={<Auth />} />
              </>
            )}
            {isUserLoggedIn && !isAdminLoggedIn && (
              <>
                {" "}
                <Route path="/user" element={<UserProfile />} />
                <Route path="/booking/:id" element={<Booking />} />
              </>
            )}
            {!isAdminLoggedIn && (
              <>
                {" "}
                <Route path="/user" element={<UserProfile />} />
                <Route path="/booking/:id" element={<Booking />} />
              </>
            )}
            {isAdminLoggedIn && !isUserLoggedIn && (
              <>
                {" "}
                <Route path="/addtheater" element={<AddTheater />} />
                <Route path="/edittheater/:id" element={<EditTheater />} />
                <Route path="/deletetheater/:id" element={<DeleteTheater />} />
                <Route path="/addshowtime" element={<AddShowtime />} />
                <Route path="/movie-analytics" element={<MovieListings />} />
                <Route path="/location-analytics" element={<LocationAnalytics />} />
                <Route path="/addmovie" element={<AddMovie />} />
                <Route path="/editmovie/:id" element={<EditMovie />} />
                <Route path="/deletemovie/:id" element={<DeleteMovie />} />
                <Route path="/user-admin" element={<AdminProfile />} />{" "}
              </>
            )}
          </Routes>
        </section>
      </div>
    </CityProvider>
  );
}

export default App;